        this.activeShop = this.shops[0].text
        this.showShop = this.shops[0].shops
        this.shopBt = this.shops[0]
    },
    methods:{
        shopText(item){
            this.activeShop = item.text
            this.showShop = item.shops
            this.shopBt = item
